import React, { Fragment } from 'react';
import Link from 'next/link';
import { Button } from 'react-bootstrap';

const Header = (props) => {
  console.log(Button);
  return (
    <Fragment>
      <div>
        <Link href="/">
          <a>Index</a>
        </Link>
        <Link href="/login">
          <a>Login</a>
        </Link>
        <i className="fa fa-search" aria-hidden="true" />
        <Button>
          asdasda
        </Button>
      </div>
      <style jsx>
        {`
          .fa-search{
            width:100px;
            color:black;
          }
        `}
      </style>
    </Fragment>
  );
};

export default Header;
